
.header{
    z-index: 100;
    
    width: 1800px;
  
    height: 100px;
    position: absolute;
    text-align: center;

    
   
}
.a-active{
    
    padding-bottom:10px ;
    border-bottom: solid 3px white;
  }
a:link{text-decoration:none; }





.logo{
    display: inline-block;
    width: 160px;
    height: 100px;
    margin: 0 auto;
    position: relative;
    top:40px;
    background-image: url('../image/toplogo.svg') ;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -webkit-background-size: 100% 100%; 
}
.icon1{
    margin-left: 100px;
display: inline-block;
   width: 100px;
   position: relative;
}
.language {
   
   position: relative;
    display: inline-block;
    width: 50px;
    height: 120px;
    -webkit-transition:all 1s;
    -o-transition:all 1s;
    transition:all 1s;
    left: 70px;
    top: 55px;
    
     
}


.language:hover  .EN{
    
  
    height: 85px;
    opacity: 1;
   
}
.CN{
    z-index: 10;
    position: relative;
 
    right: 30px;
    background:white;
    display: inline-block;
    padding-top: 5px;
    width: 35px;
    height: 30px;

    border: 1px solid white;
    border-radius: 16px;
    -moz-border-radius: 16px;;
    -webkit-border-radius: 16px;
  
}
.EN{
    position: relative;
     bottom: 20px;
    right: 30px;
    border-radius: 30px;
    -moz-border-radius: 30px;
    -webkit-border-radius: 30px;
    width: 35px;
    background-color: rgba(20, 20, 20, 0.3);
    display: block;
    line-height: 85px;
    height: 0px;
    opacity: 0;
    -o-transition:all 0.5s;
    transition:all 0.5s;
    -webkit-transition:all 0.5s;
    -moz-transition:all 0.5s;
    
    
   
    
    
    
    
    
}
.search{
    width: 200px;
}

 

#advisory {
    color: white;
    padding: 20px 50px 20px 50px;
    border: solid 2px white;
    
}

.nav {
   
   
    font-size: 1.4em;
    width: 1100px;
    margin-left: 200px;
    display: inline-block;
    color: white;
    height: 100px;
   
    
}
#nav-en{
    font-size: 1.4em;
    width: 1600px;
    
    display: inline-block;
    color: white;
    height: 100px;
}
#nav-en a{
    margin: 0 30px
}
#nav-c-en{
    width: 1600px;
}
.nav-c{
    width: 1100px;
  text-align: center;
}

.nav-2{
    display: inline-block;
    position: relative;
    left:70px ;
}
.nav-1{
    display: inline-block;
    position: relative;
    right: 80px;

}
.nav li{
    height: auto;
     display: inline-block;
    
}
.nav a {
    color: white;
    margin: 0 35px;
  
}
.language a{
 
    color: rgb(171,92,62);
    font-size: 14px;

}


