/*移动端导航*/

.nav2-bg{
    position: fixed;
   
 background:rgb(134, 140, 146);
 z-index: 990;
 opacity: 0.3;
 width: 100%;
 height: 100%;
}
.nav2-c-c{
    display: none;
}
.nav2{
    display: none;
    position: relative;
    z-index: 1000;
    background: #fbfbfb;
    width: 100%;
    height:80px;
    box-shadow: 4px 3px 4px rgb(0,0,0,0.25);
    -moz-box-shadow: 4px 3px 4px rgb(0,0,0,0.25);
    -webkit-box-shadow: 4px 3px 4px rgb(0,0,0,0.25);
}
.nav2-active{
    position: fixed;
   
    top: 0px;
}

.nav2-btn{
display: inline-block;

margin-left: 6%;
margin-top: 20px;

background:#fbfbfb; 
border-color: #fbfbfb;
border: 1px solid transparent;

}
.nav2-btn-active{
    
    margin-left: 6%;
    
    
    
    }
.nav2-logo{
    display: inline-block;
    position: absolute;
    top: 25px;
    left: 40%;
    width: 10%;
    height: 50%;
 
  

}
.nav2-xian{
    
    margin: 5px 0;
    width: 25px;
    height:3px ;
    background:  rgb(171,92,62)
}

.nav2-c{
  position: fixed;
  z-index: 999;
  left: 0px;
  top: 80px;
 text-align: left;
  width:37%;
  height: 70%;
  opacity: 1;
  background: #fbfbfb;
}
.nav2-shouye-c{
margin-top: 20px;
   
}
.nav2-c li>a{
   
   
  
    font-size: 1.5rem;
    color: #555555
}

.nav2-c li{
   
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 10%;
     width: 65%;
    border-bottom: 1px solid rgb(171,92,62);
    
    
    
    margin-left: 18%;
}

@media only screen  and (max-width:768px){
    .header{
        display: none;
    }
   
    .nav2{
        display: block;
    }
    .nav2-sc{
        position: absolute;
        top: 25px;
        right:20%;
        display: inline-block;
        width: 20px;
        height: 20px;
     
    }

}




