.zhanluehuoban-c{
   
    max-width: 1980px;
    min-width: 1900px;
    margin-bottom: 80px;
    
}
.zlhb-content-c{
    position: relative;
}


.zhanluehuoban-bgt{
    position: relative;
    max-width: 1980px;
    min-width: 1900px;
    height: 700px;
    background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -webkit-background-size: 100% 100%;
    background-image: url('../image/战略伙伴/战略伙伴banner.png')
}
.zhanluehuoban-bgt div {
    position: absolute;
    bottom:300px;
    left: 300px;
}
.zhanluehuoban-bgt h1 {
    font-weight: bold;
    font-size: 4.5em;
    letter-spacing: 1px;
    color: white;
}
.zhanluehuoban-bgt h2 {
    position: relative;
    top: 10px;
    left: 5px;
    font-size: 1.7em;
    letter-spacing: 2px;
    color: white;
}
.aside{
    position: absolute;
    top: 315px;
    left: 150px;
    height: 300px;

}
.aside-c-active{
  
  
    position: fixed !important;
    z-index: 100 !important;
    top: 10px !important;
    left: 150px !important; 
    height: 300px !important;
    

}
.aside span{
    cursor:pointer;
}
#aside2{
    letter-spacing: 8px;
}
#aside2{
    margin-left: 5px
}
.aside div{
   
    margin-bottom:10px; 
    height:60px;
    line-height: 60px;
    text-align: center;
    width: 130px;
    font-size: 1.6em;
    border-bottom: 1px solid rgba(0, 0, 0, 0.185);
}

.aside-active{
    color: rgb(171,92,62);
    border-left: 3px solid rgb(171,92,62);
    padding-left:5px; 
}

.zlhb-title{
    height: 300px;
    line-height: 300px;
    text-align: center;
    width: 1300px;
    margin: 0 auto;
}
.zlhb-title-c{
  display: inline-block;
}
.zlhb-title h1{
  
    position: relative;
    top: 120px;
    font-size: 2em;

}
.zlhb-title span{
   
    font-size: 16px;
}
[class*=zhanluehuoban-img-c]{
    position: relative;
    left: 50px;
    width: 1300px;
  

}
[class*=zlhb-img]{
