
.qiyedongtai-c{
    max-width: 1980px;
    min-width: 1900px;
}
.qydt-bgt{
    position: relative;
    max-width: 1980px;
    min-width: 1900px;
    height: 700px;
    background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -webkit-background-size: 100% 100%;
  
    background-image: url('../image/企业动态/企业动态banner.png')
}
.qydt-bgt div{
    position: absolute;
    bottom:300px;
    left: 300px;
}
.qydt-bgt h1{
    font-weight: bold;
    font-size: 4.5em;
    letter-spacing: 1px;
    color: white;
}
.qydt-bgt h2{
    position: relative;
    top: 10px;
    left: 5px;
    font-size: 1.7em;
    letter-spacing: 2px;
    color: white;
}
.qydt-content-c{
    margin-top: 100px;
    max-width: 1980px;
    min-width: 1840px;
    overflow-x: hidden;
    text-align: center;
}
.qydt-nav{
    margin: 0 auto;
    width: 800px;
    height: 70px;
    
    border-bottom: 1.2px solid rgb(159,159,159);
}
#qydt-nav-en{
    margin: 0 auto;
    width: 1000px;
    height: 70px;
    
    border-bottom: 1.2px solid rgb(159,159,159);
}
 
#qydt-nav-en a{
    letter-spacing: 1px;
}

.qydt-nav a:hover{
    color: rgb(171,92,62);
}

.qydt-nav a{
    letter-spacing: 2px;
    display: inline-block;
    height: 73px;
    font-weight: bold;
    color: #424242;
font-size: 1.5em;
  margin: 0 70px;
}
.qydt-nav-active {
    color:  rgb(171,92,62) !important;
    border-bottom: 7px solid rgb(171,92,62)!important;
}



.qydt-content{
 margin-top: 30px;
}
.qydt-c{
    display: inline-block;
    width: 1200px;
    height: 270px;
    margin: 0 auto;
    border-bottom: 1.2px solid rgb(159,159,159);
}
.qydt-c5{
    margin-bottom: 100px;
}
.qydt-c3{
    margin-bottom: 100px;
}
.qydt-c a{
    display: inline-block;
    height: 270px;
    
}
[class*=qydt-img]{
    vertical-align:middle;
    display: inline-block;
    width: 301px;
    height: 184px;
 
}
.qydt-img-ifm1{
    background-image: url('../image/企业动态/新闻中心封面图/1.jpg');
    background-repeat: no-repeat;
    
}
.qydt-img-ifm2{
    background-image: url('../image/企业动态1.png');
    background-repeat: no-repeat;
    
}
.qydt-img-ifm3{
    background-image: url('../image/企业动态/新闻中心封面图/3.jpg');
    background-repeat: no-repeat;
    
}
.qydt-img-ifm4{
    background-image: url('../image/企业动态/新闻中心封面图/4.jpg');
    background-repeat: no-repeat;
    
}
.qydt-img-ifm5{
    background-image: url('../image/企业动态/新闻中心封面图/5.jpg');
    background-repeat: no-repeat;
    
}
.qydt-img-xx1{
    background-image: url('../image/企业动态/新闻中心封面图/6.jpg');
    background-repeat: no-repeat;
    
}
.qydt-img-xx2{
    background-image: url('../image/企业动态/新闻中心封面图/7.jpg');
    background-repeat: no-repeat;
    
}
.qydt-img-xx3{
    background-image: url('../image/企业动态/新闻中心封面图/8.jpg');
    background-repeat: no-repeat;
    
}

.qydt-font{
    display: inline-block;
    vertical-align:middle;
    width: 650px;
    height: 160px;
   
}
.qydt-font h1{
    font-weight: bold;
    margin-left: 30px;
    text-align: start;
    text-align: left;
    font-size: 1.4em;
}
.qydt-font p{
    color: #676767;
    margin-left: 30px;
    margin-top: 20px;
    line-height: 24px;
    text-align: left;
    font-size: 1em;
}
.qydt-time-c{
    position: relative;
    margin-left: 50px;
    display: inline-block;
    vertical-align: middle;
    width: 100px;
    height: 270px;
   
}
.qydt-time-c div{
    margin-top: 100px;
    border-left: 1.2px  solid rgb(159,159,159)
}
.qydt-time-c h1{
    letter-spacing: 2px;
    font-size: 2em;
}
.qydt-time-c h2{
    margin-top: 10px;
    font-size: 20px;
}
.qydt-time-c a {
    position: absolute;
    left: 5px;
    bottom: 30px;
    display: inline-block;
    border:  1.2px solid rgb(159,159,159);
    border-radius: 10px;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
     width: 100px;
     height: 30px;
     line-height: 30px;
     color: rgb(113 ,113, 113);
}
#news-more{
    font-size: 17px;
}
.qydt-time-c a:hover{
    color: rgb(171,92,62)
}
.qydt-pages{
    height: 200px;
    line-height: 200px;
}
@media only screen  and (max-width:1700px){
    .qiyedongtai-c{
       
        max-width: 1700px;
        min-width: 1500px;
    }
    .qydt-content-c {
        margin-top: 100px;
      
        max-width: 1700px;
        min-width: 1500px;
        overflow-x: hidden;
        text-align: center;
    }
    .qydt-bgt{
       
      
        max-width: 1700px;
        min-width: 1500px;
        
    }
}
@media only screen  and (max-width: 1500px) {
    .qiyedongtai-c{
       max-width: 1500px;
        min-width: 1300px;
    }
    .qydt-content-c {
        margin-top: 100px;
        max-width: 1500px;
        min-width: 1300px;
        overflow-x: hidden;
        text-align: center;
    }
    .qydt-bgt{
       
        max-width: 1500px;
        min-width: 1300px;
        
    }
    .qydt-font h1 {
       
        font-size: 1.2em;
    }
}
@media only screen  and (max-width: 1300px) {
    .qiyedongtai-c{
       max-width: 1300px;
        min-width: 1100px;
    }
    .qydt-content-c {
        margin-top: 100px;
        max-width: 1300px;
        min-width: 1100px;
        
        text-align: center;
    }
    .qydt-bgt{
       
        max-width: 1300px;
        min-width: 1100px;
        
    }
    
}

@media only screen and (min-width:1000px)  and (max-width: 1100px) {
    .qiyedongtai-c{
       
        max-width: 1100px;
        min-width: 900px;
    }
    .qydt-bgt div {
      
        left: 200px;
    }
    .qydt-c {
       
        width: auto;
        
    }
    .qydt-font {
        
            display: inline-block;
            vertical-align: middle;
            width: 500px;
            height: 160px;
        
    }
    [class*=qydt-img] {
        vertical-align: middle;
        display: inline-block;
        width: 241px;
        height: 144px;
    }
    .qydt-content-c {
        margin-top: 100px;
        max-width: 1100px;
        min-width: 900px;
        overflow-x: hidden;
        text-align: center;
    }
    .qydt-bgt{
       
        max-width: 1100px;
        min-width: 900px;
        
    }
}


