

.news-nav{
    position: relative;
    
    width: 1600px;
    margin: 0 auto;
    border-bottom: 1px solid black;
}
.news-navt{
    text-align: left;
    position: relative;
    right: 600px;
    display: inline-block;

}
.news-navt h2{
    
    font-size: 30px;
}
.news-nav h1{
    margin-top: 5px;

    font-size: 2em;
    color: rgb(171,92,62);
    
}
.news-nav2{
    position: relative;
    left: 600px;
    bottom: 10px;
    display: inline-block;
}
.news-nav2 div{
    width: 80px;
    height: 40px;
    line-height: 40px;
    margin: 0 10px;
    display: inline-block;
    color: white;
    background: rgb(171,92,62);

}
.news-content-c{
 
    border-bottom: 1px solid #e3e3e3;
