 /*轮播图样式*/
.carousel-control{
  z-index: 100;

}
 .carousel-indicators li{
   background: white;
   width: 15px;
   height: 15px;
   border-radius:50%;
   -moz-border-radius:50%;
  -webkit-border-radius:50%;  
   margin: 0 20px;
 }
 .carousel-indicators .active{
   position: relative;
   top: 2px;
  margin: 0 20px;
  border-radius:50%;
  -moz-border-radius:50%;
  -webkit-border-radius:50%; 
   width: 20px;
   height: 20px;
   border: 3px solid #fff;
   background: no-repeat;
 }
.content-img1{
   background-image: url('../image/内容.png');
   width:1900px ;
   background-repeat: no-repeat;
  
   height: 350px;
   position: absolute;
   bottom: 165px;
 
}
