
.rencaizhanlue-c{
    
    max-width: 1980px;
    min-width: 1900px;
    position: relative;
}
.rencaizhanlue-bgt{
    position: relative;
    max-width: 1980px;
    min-width: 1840px;
    height: 700px;
    background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -webkit-background-size: 100% 100%;
    background-image: url('../image/人才战略/人才战略_01.jpg')
}
.rencaizhanlue-bgt div {
    position: absolute;
    bottom:300px;
    left: 300px;
}
.rencaizhanlue-bgt h1 {
    font-weight: bold;
    font-size: 4.5em;
    letter-spacing: 1px;
    color: white;
}
.rencaizhanlue-bgt h2 {
    position: relative;
    top: 10px;
    left: 5px;
    font-size: 1.7em;
    letter-spacing: 2px;
    color: white;
}
.rencaizhanlue-content-c{
    text-align: center;
}
.rencaizhanlue-title{
    margin-top: 150px;
    height: 100px;
}
.rencaizhanlue-title span{
    font-size: 20px;
    font-weight: bold;
    color: rgb(171,92,62);
}
.rencaizhanlue-title h1{
    font-size: 2.5em;
    color: rgb(171,92,62);
}
.rczl-content1{
    
    width: 1200px;
    text-align: center;
    font-size: 18px;
    line-height: 35px;
    margin: 0 auto;
    margin-top: 40px;
}
.rczl-content2{
    background: rgb(233,233,233);
    width: 1350px;
    height: 600px;
  
    line-height: 600px;
    position: relative;
    margin: 0 auto;
    margin-top: 100px;
    display: inline-block;
    vertical-align: middle;
}

.rczl-content2-aside{
    vertical-align: middle;
    display: inline-block;
    position: relative;
    left: 60px;
    width: 300px;
    height: 470px;
    
}
.rczl-content2-a{
    -o-transition:all 0.4s;
    transition:all 0.4s;
    -webkit-transition:all 0.4s;
	-moz-transition:all 0.4s;
    position: relative;
    margin-top: 20px;
   background: white;
    width: 210px;
    height: 130px;
}
.rczl-content2-a h1{
    
    margin-left: 15px;
   position: relative;
   top: 10px;
    text-align: left;
 font-size: 20px;
}
.rczl-content2-a p{
    margin: 0 auto;
    height: 100px;
    line-height: 20px;
    width: 180px;
    margin-top: 20px;
    text-align: left;
}
.rczl-round{
    position: absolute;
    bottom: 10px;
    left: 10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    background:rgb(171,92,62) ;
}
.rczl-content2-a:hover{
    background: rgb(171,92,62);
}
.rczl-content2-a:hover .rczl-round{
    background: white;
}
.rczl-content2-a:hover .rczl-jt {
    border-top: 2.1px solid rgb(171,92,62);
    border-right: 2.1px solid rgb(171,92,62);
}
.rczl-content2-a:hover  h1{
    color: white;
}
.rczl-content2-a:hover  p{
    color: white;
}
.rczl-jt{
    position: relative;
   left: 5px;
   top: 7px;
    width: 10px;
    height: 10px;
    border-top: 2.1px solid #ffffff;
    border-right: 2.1px solid #ffffff;
    transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
}
.rczl-content2-img{
    vertical-align: middle;
    width: 600px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -webkit-background-size: 100% 100%;
    height: 380px;
    -webkit-transform: scale(1,1.13);
        -ms-transform: scale(1,1.13);
            transform: scale(1,1.13);
    background-repeat: no-repeat;
    background-image:url('../image/人才战略/人才战略_04.jpg') ;
    box-shadow: 4px 4px 4px  #d6d6d6f3;
    -moz-box-shadow: 2px 2px 2px   #d6d6d6;
    -webkit-box-shadow: 4px 3px 2px   #d6d6d6;
    display: inline-block;
}

.rczl-content3-nav{
   position: relative;
    width: 1620px;
    margin: 0 auto;
    margin-top: 250px;
    border-bottom: 1px solid black;
}
.rczl-content3-nav1 {
    position: absolute;
    left: 100px;
    bottom: 10px;
}
.rczl-content3-nav1 h1{
    font-size: 25px;
   margin-bottom: 10px;
}
.rczl-content3-nav1 h2{
    text-align: left;
