    font-weight: bold;
    color: rgb(171,92,62);
}
.rczl-content3-nav2{
 position: absolute;
 right: 150px;
 bottom: 10px;
}
.rczl-content3-nav2 span{
    display: inline-block;
    margin: 0 10px;
    width: 80px;
    height: 34px;
    line-height: 34px;
    color: white;
    background: rgb(171,92,62);
}
[class*=rczl-content3-m]{
    text-align: left;
     
