

.footer{
    max-width: 1980px;
    min-width: 1900px;
    height: 400px;
    text-align: center;
    background: #242424
   
}
.footer-c{
 
    border-bottom: 1px solid #656464;
    height: 300px;
    display: inline-block;
   width:100%;
    position: relative;
    margin:  0 auto;
    margin-top: 30px
 
    
   
}
.footer-c h1{
    font-size: 1.2em;
    color: white;
    margin-bottom: 5px;
}
#footer-c-en h1{
    font-size: 1.6em;
    color: white;
    margin-bottom: 5px;
}
.footer-c h1>a{
    color: white;
    
    
}
[class*=list-c] li>a:hover{
    text-decoration:underline;
    color: white;
    
}
.list-c-c{
    position: relative;
    right: 100px;
    display: inline-block;
}
#list-c-en{
    position: relative;
    right: 30px;
}
.footer-c ul{
   
   
    width: 260px;
    height: 200px;
    text-align: left;
    display: inline-block;
    vertical-align: middle;

 
}
.footer ul>li{
    margin-top: 15px;
    color:  #656464;
}
.footer ul>li>a{
   
    color:  #656464;
    font-size: 14px;
}
#wechat-number{
    width: 310px;
}
.erweima-c{
    position: relative;
    top: 60px;
    display: inline-block;
    left: 100px;
    width: 150px;
    height: 160px;
}
.erweima-c-en{
    position: relative;
    top: 60px;
    display: inline-block;
    left: 100px;
    width: 150px;
    height: 160px;
}
.erweima{
   
    left: 110pX;
    width: 150px;
    height: 150px;
    background-image: url('../image/qrcode_for_gh_5b2c54f9a81a_344.jpg');
    background-size: 100% 100%;
}
.erweima-font{
    color: #656464;
}
.bdsharebuttonbox{
    position: relative;
    bottom: 10px;
    margin-left: 10px;
    display: inline-block;
}

.bdsharebuttonbox div{
    
    cursor:pointer;
    display: inline-block;
}
.weixin-c{
    position: relative;
    top: 10px;
}
.sina-c{
    position: relative;
    top: 13px;
}
.bdsharebuttonbox span{
    position: relative;
    top: 10px;
   margin: 0 10px;
   color: white;
  
}
#bds_tsina{
    color: white;
    background: no-repeat;
    position: relative;
}
#bds_weixin{
    
    color: white;
    position: relative;
    background: no-repeat;
    
}
.share-en{
   position: relative;
}
.share-to-en{
    display: inline-block;
    position: absolute;
    bottom: 10px;
}
.bdsharebuttonbox{
    position: absolute;
    right: 10px;
    bottom: 15px;
}
.share{
  position: relative;
  height: 30px;
  
  
}
.share-to-en{
 display: inline-block;

}

.wx-img-c{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: white;
    display: inline-block;
}
.bds_weixin span{
    margin-left: 5px;
}
.bds_tsina span{
    margin-left: 5px;
}
.sina-img{
    position: relative;
    right: 2px;
    bottom: 2px
}
.sina-img-c{
    width: 30px;
    height: 30px;
    border-radius:15px;
    background: white;
    display: inline-block;

}
 .bd_weixin_popup_foot {
    font-size: 12px;
    text-align: left;
    color: #666;
}
.phone-number-en{
    width: 250px;
}
@media only screen and (max-width:1700px){
    .footer{
      
        
        min-width: 1500px;
       height: 370px;
       
    }
    .footer-c ul {
        width: 200px;
        height: 200px;
        text-align: left;
        display: inline-block;
        vertical-align: middle;
    }
    .share{
        width: 250px;
    }
    .list-c-c {
        
        right: 80px;
      
    }
    .erweima-c {
      
        left: 70px;
      
    }
    .erweima-c-en{
        
        left: 110px;
       
    }
    
    .footer-c{
        margin: 0 auto;
      
        margin-top: 15px;
       
    }
}
@media only screen and (max-width: 1500px) {
 
    .erweima{
       
     
        width: 100px;
