        top: 100px;
      left: 70px;
        width: 100px;
     
    }
    .footer{
      
        max-width: 1500px;
        min-width: 1100px;
        
        height: 350px;
        text-align: center;
        background: #242424
       
    }
    .footer-c{
    
         margin-top: 10px;
